import kmLi from '../../components/km-li.vue'
export default {
	data() {
		return {
			url:'',
			link:'',
			userinfo:'',
			user_id:0,
			status:1,	// 0:审核中；1:未接单；2：拒绝；3:待确定；4：运输中（出货）；5：已完成（收货）；6 已支付；7：待支付 //0:未接单；1:待确认；2：已退款；3：运输中；4：已完成；5：申诉中
			orderId:'',
			order:''
		}
	},
	components: {
		kmLi
	},
	filters:{
		filterMath(value) {
			return Math.round(value*100)/100
		}
	},
	onLoad(e) {
		var _this = this;
		
		this.userinfo = uni.getStorageSync('userinfo');
		this.user_id  = this.userinfo.user_id;
		
		this.orderId = e.orderId;
		this.url = this.serverURL;
		
		/* uni.request({
			url: this.url + 'home/index/get_transportation_order',
			method:'POST',
			data:{
				tid: _this.orderId
			},
			success(res) {
				console.log(res);
				if(res.data.code==100){
					var order = res.data.list;
					_this.order = order;
				}
			},
			fail(error) {
				console.log(error)
			}
		}) */
		
	},
	onShow() {
		var _this = this;
		uni.request({
			url: this.url + 'home/index/get_transportation_order',
			method:'POST',
			data:{
				tid: _this.orderId
			},
			success(res) {
				console.log(res);
				if(res.data.code==100){
					var order = res.data.list;
					_this.order = order;
				}
			},
			fail(error) {
				console.log(error)
			}
		})
	},
	filters:{
		filterPhone(val) {
			return val.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2');
		}
	},
	methods: {
		takeToken() {	//接单
			var _this = this;
			uni.showLoading({
				title:'接单中...',
				mask:true
			})
			uni.request({
				url: this.url + 'home/Order/driver_add_order',
				method:'POST',
				data:{
					user_id: _this.user_id,
					tran_id: _this.orderId
				},
				success(res) {
					console.log(res);
					if(res.data.code==100){
						uni.showModal({
							//content: "接单成功，请耐心等待货主确认。",
							content:res.data.msg,
							confirmText: "确定",
							showCancel:false
						})
					} else {
						uni.showToast({
							//title:"等待货主确认中，请不要重复接单",
							title:res.data.msg,
							mask:true,
							icon:"none",
							duration:2000
						})
					}
				},
				fail(error) {
					console.log(error)
				},
				complete() {
					uni.hideLoading();
				}
			})
			
		}
	}
}
